<template>
  <div class="home full-height">
    <div class="container py-4 full-height">
      <div class="row full-height">
        <div class="col-8 full-height">
          <div ref="sketch" class="full-height">
            <vue-p5 @sketch="sketch"></vue-p5>
          </div>
        </div>
        <div class="col-4">
          <h3>Controls</h3>

          <Point class="my-2" v-model="p" readonly :size="sketchSize">P</Point>

          <Point class="my-2" v-model="left" :size="sketchSize">Left motor</Point>
          <Point class="my-2" v-model="right" :size="sketchSize">Right motor</Point>

          <div class="card card-body p-2">
            <div class="row">
              <div class="col">
                Left R <span class="badge badge-primary">{{ left.r.toFixed(2) }}</span>
                <input type="range" v-model.number="left.r" min="0" :max="maxR" />
              </div>
              <div class="col">
                Right R <span class="badge badge-primary">{{ right.r.toFixed(2) }}</span>
                <input type="range" v-model.number="right.r" min="0" :max="maxR" />
              </div>
            </div>
          </div>

          <Point class="my-2" v-model="p1" :size="sketchSize">P1</Point>
          <Point class="my-2" v-model="p2" :size="sketchSize">P2</Point>

          <div class="card card-body p-2">
            <h6>Movement</h6>
            <div class="row">
              <div class="col-6">
                <a href="#" class="btn btn-primary my-1 full-width" @click.prevent="moveTo(p1, 200)">Move to P1</a>
              </div>
              <div class="col">
                <a href="#" class="btn btn-primary my-1 full-width" @click.prevent="moveTo(p2, 200)">Move to P2</a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/// <reference path="../p5.d.ts" />
import VueP5 from "vue-p5"
import Point from "@/components/Point.vue"
import _ from "lodash"

const DEFAULT_POINT_SIZE = 10

const distance = (p1, p2) => {
  const dx = p2.x - p1.x
  const dy = p2.y - p1.y
  return Math.sqrt(dx * dx + dy * dy)
}

class History {
  constructor(length) {
    this._data = []
    this._data.length = length
    this._head = 0
  }
  last() {
    return this._data[(this._head - 1) % this._data.length]
  }
  push(el) {
    this._data[this._head++ % this._data.length] = el
  }
  *values() {
    for (let i = 0; i < this._data.length; i++) {
      const value = this._data[(this._head + i) % this._data.length]
      if (value != undefined) yield value
    }
  }
}

export default {
  name: "Home",
  components: { VueP5, Point },
  data() {
    return {
      left: {
        x: 10,
        y: 15,
        r: 200,
      },
      right: {
        x: 450,
        y: 30,
        r: 380,
      },
      p1: { x: 100, y: 120 },
      p2: { x: 380, y: 452 },
      sketchSize: {
        width: 0,
        height: 0,
      },
    }
  },
  computed: {
    maxR() {
      return distance({ x: 0, y: 0 }, { x: this.sketchSize.width, y: this.sketchSize.height })
    },
    p() {
      const r0 = this.left.r
      const r1 = this.right.r
      const cx0 = this.left.x
      const cy0 = this.left.y
      const cx1 = this.right.x
      const cy1 = this.right.y

      const d = distance(this.left, this.right)

      const a = (r0 ** 2 - r1 ** 2 + d ** 2) / (2 * d)
      const h = Math.sqrt(r0 ** 2 - a ** 2)

      const cx2 = cx0 + (a * (cx1 - cx0)) / d
      const cy2 = cy0 + (a * (cy1 - cy0)) / d

      const x = cx2 - (h * (cy1 - cy0)) / d
      const y = cy2 + (h * (cx1 - cx0)) / d

      return { x, y }
    },
  },
  methods: {
    /**
     * @param {p5} sk
     */
    sketch(sk) {
      const history = new History(200)
      const drawPoint = ({ x, y }, r, g, b, size = DEFAULT_POINT_SIZE, stroke = true) => {
        if (stroke) sk.stroke(0)
        else sk.noStroke()

        sk.fill(r, g, b)
        sk.circle(x, y, size)
      }

      const drawCircleRadius = (p, radius, r = 0, g = 0, b = 0) => {
        sk.noFill()
        sk.stroke(r, g, b)
        sk.circle(p.x, p.y, radius * 2)
      }

      const drawLine = (p1, p2) => {
        sk.noFill()
        sk.stroke(0)
        sk.line(p1.x, p1.y, p2.x, p2.y)
      }

      sk.setup = () => {
        this.sketchSize.width = this.$refs.sketch.clientWidth
        this.sketchSize.height = this.$refs.sketch.clientHeight
        sk.resizeCanvas(this.sketchSize.width, this.sketchSize.height)
      }

      sk.windowResized = () => {
        try {
          this.sketchSize.width = this.$refs.sketch.clientWidth
          this.sketchSize.height = this.$refs.sketch.clientHeight
          sk.resizeCanvas(this.sketchSize.width, this.sketchSize.height)
          // eslint-disable-next-line no-empty
        } catch {}
      }

      sk.draw = () => {
        sk.background(255)
        sk.noFill()
        sk.stroke(0)
        sk.rect(0, 0, sk.width, sk.height)
        drawPoint(this.left, 255, 0, 0, DEFAULT_POINT_SIZE * 1.5)
        drawPoint(this.right, 0, 255, 0, DEFAULT_POINT_SIZE * 1.5)
        drawPoint(this.p1, 0, 0, 255)
        drawPoint(this.p, 255, 0, 255)
        drawPoint(this.p2, 0, 255, 255)

        sk.noFill()
        drawCircleRadius(this.left, this.left.r)
        drawCircleRadius(this.right, this.right.r)

        drawLine(this.left, this.p)
        drawLine(this.right, this.p)

        if (!_.isEqual(this.p, history.last())) history.push({ ...this.p })

        for (let p of history.values()) {
          drawPoint(p, 100, 100, 100, 3, false)
        }
      }
    },
    /**
     * @param {Number} speed px/s
     */
    async moveTo(p, speed) {
      const time = (distance(this.p, p) / speed) * 1000
      const timeStep = 16.0
      let ticks = time / timeStep

      const dr0 = (distance(this.left, p) - this.left.r) / ticks
      const dr1 = (distance(this.right, p) - this.right.r) / ticks

      while (ticks > 0) {
        this.left.r += dr0
        this.right.r += dr1
        ticks--
        await new Promise(resolve => setTimeout(resolve, timeStep))
      }
    },
  },
}
</script>

<style lang="scss" scoped>
.full-height {
  height: 100%;
}
.full-width {
  width: 100%;
}
.progress-bar {
  transition: none;
}
</style>
